'use strict';

import mongoose from 'mongoose';

var ChampionSchema = new mongoose.Schema({
  name: String,
  name_ar: String,
  subname:String,
  subname_ar:String,
  event_start:String,
  event_end:String,
  month:String,
  year:String,
  files:Object,
  info: String,
  active: Boolean,
  champion:String,
  user: String,
  status: Number,
  orderNo: String
  
});

export default mongoose.model('Champion', ChampionSchema);
